  border-radius: 10px;
  border-style: solid;
  border-width: 3px;
  border-color: rgba(196, 196, 196, 1);
  margin: 0px 1.25rem 0px;
  border-radius: 0.5rem;
  width: 21.875rem;
  height: 14.438rem;
}

