  height: 2.5rem;
  display: flex;
  justify-content: space-between;
}

button {
  all: unset;
  height: 2.5rem;
  border-radius: 5px;
}

#button_save {
  border-style: solid;
  border-width: thick;
  border-color: gray;
  width: 9.625rem;
}
